import "./styles.css";
import React, { useState } from "react";
import MultipleValueTextInput from "react-multivalue-text-input";
import getDomain from "./api";
import Results from "./Results";

let logo = "https://i.imgur.com/AMREhFA.png";

export default function App() {
  const [domainQuery, setDomainQuery] = useState([]);
  const [results, setResults] = useState([]);
  const [errResults, catchErrResults] = useState([]);

  const handleAdd = (domain) => {
    setDomainQuery([...domainQuery, domain]);
  };

  const handleDelete = (deletedDomain, i) => {
    let newArr = i.filter((domain) => domain !== deletedDomain);
    setDomainQuery(newArr);
  };

  const getResults = () => {
    setResults([]);
    catchErrResults([]);

    domainQuery.map((domain, i) =>
      getDomain(domain)
        .then((res) => setResults((results) => [...results, res.data]))
        .catch(() => catchErrResults((errResults) => [...errResults, [domain]]))
    );
  };

  const clearResults = () => {
    setResults([]);
    catchErrResults([]);
  };

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} alt="logo" />
        <h4>Rank Domain Assessment</h4>
      </header>

      <MultipleValueTextInput
        name="domain-input"
        onItemAdded={handleAdd}
        onItemDeleted={handleDelete}
        values={domainQuery.map((i) => i)}
        placeholder="Separate with COMMA, SPACE, or ENTER"
        disabled={domainQuery.length === 10 ? true : false}
        charCodes={[13, 44, 32]}
      />
      <p className="search-limit">
        Limit {10 - domainQuery.length} domain searches.
      </p>

      <Results errResults={errResults} results={results} />

      <div className="func-button">
        {results.length > 0 ? (
          <button onClick={clearResults}>Clear</button>
        ) : (
          <button onClick={getResults}>Get Results</button>
        )}
      </div>
    </div>
  );
}
