import "./styles.css";
import React from "react";

const Results = (props) => {
  const { results, errResults } = props;

  const higher = results.filter((result) => result.rank < 100);
  const lower = results.filter((result) => result.rank > 100);

  return (
    <div className="results">
      <p className="higher">
        {higher.map((result, i) => (
          <li key={i}>
            {result.domain} #{result.rank}{" "}
            <span aria-label="trophy-emoji" role="img">
              üèÜ
            </span>
          </li>
        ))}
      </p>

      <p className="lower">
        {lower.map((result, i) => (
          <li key={i}>
            {result.domain} #{result.rank}
          </li>
        ))}
      </p>

      <p className="error">
        {errResults.map((err, i) => (
          <li key={i}>‚ö† {err}</li>
        ))}
      </p>
    </div>
  );
};

export default Results;
